/*contact*/

.contact {
    text-align: center;
}

.contact h3 {
    font-size: 1.8rem;
    color: #34ebc3;
    padding: 0;
    margin: 0;
    border: 0;
}

.contact form {
    display: inline-block;
    width: 60%;
}

.contact input, .contact textarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: none;
}

.contact button {
    padding: 10px 20px;
    background-color: #34ebc3;
    color: #000;
    font-size: 1.2rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.contact button:hover {
    background-color: #2bbca1;
}

/*general*/
.rainbow-color{
    background: linear-gradient(
            90deg,
            rgba(28, 245, 206, 0.8) -15.93%,
            rgba(248, 7, 50, 0.8) 93.16%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    font-size: 2rem;
    font-family: Bakbak One;
    font-weight: bold;
}


span.bold{
    font-weight: bold;
    font-family: Bakbak One;
    color: #34ebc3;
    font-size: 1.2rem;
}


svg{
    position: absolute;
    top: -2%;
    left: 37%;
    width: 30%;
    height: 30%;
    z-index: -1;
}









